import { StorageService } from './storage.service';
import { UserSwipe } from '../enums';
import { TImageFileName, TImagePath } from '../types';

export type TUserChoiseItem = {
  photo: TImageFileName;
  decision: UserSwipe;
};

export type TUserChoiseData = Array<TUserChoiseItem>;

export class UserDecisionStorageService {
  private storage = StorageService.makeInstance<TUserChoiseData>('userdecision', []);

  setItem(photoPath: TImagePath, decision: UserSwipe): void {
    const data = this.storage.getState();
    const photoFileName = photoPath.split('/').reverse()[0];
    const itemToSave = { photo: photoFileName, decision };

    data.push(itemToSave);

    this.storage.setState(data);
  }

  getItems(): TUserChoiseData {
    return this.storage.getState();
  }

  clear(): void {
    this.storage.setState([]);
  }
}
