import { TProject } from 'projects';
import { BehaviorSubject } from 'rxjs';
import { staticProjectsStorage } from './static-projects.storage';
import {
  TUserChoiseItem,
  UserDecisionStorageService
} from './user-decision-storage.service';
import { UserSwipe } from '../enums';
import { TSelectionProjectItem } from '../types';
import {
  fetchImagePromise,
  fetchImages
} from '../utils';

export type TItems = Array<TSelectionProjectItem>;

export type TUserDecisionServiceData = {
  data: TItems | void;
  error: Error | void;
};

export class UserDecisionService {
  private static initialData: TUserDecisionServiceData = { data: void 0, error: void 0 };

  data$ = new BehaviorSubject<TUserDecisionServiceData>(UserDecisionService.initialData);

  constructor(private storage: UserDecisionStorageService) {}

  getItems(): void {
    if (this.data$.getValue().data) {
      return;
    }

    const projects: Array<TSelectionProjectItem> = staticProjectsStorage.projects$.getValue()
      .filter(({ include }: TProject) => include)
      .map((project: TProject) => {
        const selection = project.selection
          .map((photo: string) =>
            ({ photo, projectId: project.id, projectName: project.name }));

        return selection;
      })
      .flat()
      // eslint-disable-next-line @typescript-eslint/no-magic-numbers
      .sort(() => Math.random() - 0.5)
      // eslint-disable-next-line @typescript-eslint/no-magic-numbers
      .sort(() => Math.random() - 0.5);

    const data = this.filterAlreadyChoosedItems(projects);

    if (data.length) {
      const images = data.map((item: TSelectionProjectItem) => item.photo);
      const firstImage = images.shift() as string;

      void fetchImagePromise(firstImage).then(() => data)
        .then((getProjectsForSelectionResult: TItems) =>
          this.data$.next({ data: getProjectsForSelectionResult, error: void 0 })
        );

      fetchImages(images);
    }

    this.data$.next({ data, error: void 0 });
  }

  setItem(item: TSelectionProjectItem, decision: UserSwipe): void {
    this.storage.setItem(item.photo, decision);

    const { data } = this.data$.getValue();

    if (data) {
      const filtered = data.filter((dataItem: TSelectionProjectItem) => dataItem.photo !== item.photo);
      this.data$.next({ data: filtered, error: void 0 });
    }
  }

  reset(): void {
    this.data$.next(UserDecisionService.initialData);
  }

  private filterAlreadyChoosedItems(itemsFromResponse: Array<TSelectionProjectItem>): TItems {
    const swipedItems = this.storage.getItems().map((item: TUserChoiseItem) => {
      const project = staticProjectsStorage.getProjectByPhoto(item.photo);

      return { photo: item.photo, projectId: project.id, projectName: project.name };
    });

    if (!swipedItems.length) {
      return itemsFromResponse;
    }

    return itemsFromResponse.filter((itemFromResponse: TSelectionProjectItem) =>
      swipedItems.every((swipedItem: TSelectionProjectItem) =>
        swipedItem.photo !== itemFromResponse.photo
      ));
  }
}
