/* eslint-disable @typescript-eslint/no-unsafe-assignment */
/* eslint-disable @typescript-eslint/no-unsafe-call */
import { TProject } from 'projects/project.type';
import { BehaviorSubject } from 'rxjs';
import { imageStorage } from './image.storage';
import { ImageType } from '../enums';
import { TImageFileName, TSelectionProjectItem } from '../types';
import { extractImageFileName, getAssetsPath } from '../utils';

const numberRegexp = /\d+/;

type TMappedKeys = {
  project: Omit<TProject, 'photos'>;
  photos: TProject['photos'];
};

type TRequirePhotoContextRes = {
  src: string;
  preSrc: string;
};

class StaticProjectsStorage {
  projects$ = new BehaviorSubject<Array<TProject>>([]);

  getProjects(): void {
    const required = this.requireProjectsAndPhotos();
    const projects = this.mapRequiredInfoToProjectModel(required);

    this.projects$.next(projects);
  }

  getProjectById(projectId: string): TProject {
    return this.projects$.getValue().find(({ id }: TProject) => id === projectId) as TProject;
  }

  getProjectByPhoto(photo: TImageFileName): TProject {
    const { projectId } = extractImageFileName(photo);

    return this.getProjectById(projectId);
  }

  getSelectionItemByPhoto(photo: TImageFileName): TSelectionProjectItem {
    const project = this.getProjectByPhoto(photo);

    return {
      photo: `${getAssetsPath()}${photo}`,
      projectId: project.id,
      projectName: project.name
    };
  }

  private getWebpackRequireRequest(): __WebpackModuleApi.RequireContext {
    return require.context('projects', true, /\.(json|png|jpe?g|svg)$/);
  }

  private requireProjectsAndPhotos(): Record<string, TMappedKeys> {
    const result: Record<string, TMappedKeys> = {};

    const r = this.getWebpackRequireRequest();

    r.keys().forEach((path: string) => {
      const projectId = this.getProjectIdFromPath(path);

      if (!result[projectId]) {
        result[projectId] = {
          project: {} as Omit<TProject, 'photos'>,
          photos: []
        };
      }

      if (path.includes('json')) {
        result[projectId].project = r(path) as Omit<TProject, 'photos'>;
      }

      if (path.includes('photos')) {
        const { src, preSrc } = r<TRequirePhotoContextRes>(path);

        imageStorage.setImageBase64Data(src, preSrc);

        result[projectId].photos.push(src);
      }
    });

    return result;
  }

  private mapRequiredInfoToProjectModel(requiredInfo: Record<string, TMappedKeys>): Array<TProject> {
    return Object.values<TMappedKeys>(requiredInfo)
      .map(({ project, photos }: TMappedKeys) => {
        const path = getAssetsPath();
        const cover = `${path}${project.cover}`;

        const selection = project.selection
          .map((selectionPhoto: string) => `${path}${selectionPhoto}`);

        const sortedPhotos = photos.sort((a: string, b: string) => {
          const { type: aType, index: aIndex } = extractImageFileName(a);
          const { type: bType, index: bIndex } = extractImageFileName(b);

          if (aType === ImageType.DRAW && bType === ImageType.IMAGE) return 1;
          if (aType === ImageType.IMAGE && bType === ImageType.DRAW) return -1;

          return aIndex - bIndex;
        });


        return { ...project, cover, selection, photos: sortedPhotos };
      });
  }

  private getProjectIdFromPath(path: string): string {
    const match = numberRegexp.exec(path);

    return match ? match[0] : '';
  }
}

export const staticProjectsStorage = new StaticProjectsStorage();

staticProjectsStorage.getProjects();
