import { ImageType } from '../enums';
import { TImageFileName } from '../types';

export type TExtractImageFileNameResult = {
  type: ImageType;
  projectId: string;
  index: number;
};

export function extractImageFileName(imageFileNameWithExt: TImageFileName): TExtractImageFileNameResult {
  const [imageFileName] = imageFileNameWithExt.split('.');
  const [projectId, type, index] = imageFileName.split('_');

  return {
    type: type as ImageType,
    index: Number(index),
    projectId
  };
}
